import React, { Component } from 'react';
import Cookies from 'universal-cookie';
const cookies = new Cookies();

class Dashboard extends Component {
  constructor(props) {
    super(props);

    this.toggle = this.toggle.bind(this);
    this.onRadioBtnClick = this.onRadioBtnClick.bind(this);

    this.state = {
      dropdownOpen: false,
      radioSelected: 2,
      isLoggedIn: false
    };
  }

  toggle() {
      this.setState({
        dropdownOpen: !this.state.dropdownOpen,
      });
  }

  onRadioBtnClick(radioSelected) {
      this.setState({
        radioSelected: radioSelected,
      });
  }

  componentWillMount(){
    if(cookies.get('token') !== undefined && cookies.get('token') !== null){
      this.setState({
        isLoggedIn: true
      });
    }
  }

  loading = () => <div className="animated fadeIn pt-1 text-center">Loading...</div>

  render() {
    // if(this.state.isLoggedIn){
      return (
        <div className="animated fadeIn">
          Our core values underpin all that we do. They are:
          <div style={{display: 'flex', height: '40vh'}}>
            <div style={{flex: 1, flexDirection: 'column', margin: '50px'}}>
              <img src={'assets/img/comps/comp1.png'} style={{width: '100%'}}/>
              <b>Compassion - </b>We are sensitive, understanding and caring in our service of all people.
            </div>
            <div style={{flex: 1, flexDirection: 'column', margin: '50px'}}>
              <img src={'assets/img/comps/comp2.png'} style={{width: '100%'}}/>
              <b>Integrity - </b>We are honest, accountable and transparent in our work and relationships.
            </div>
            <div style={{flex: 1, flexDirection: 'column', margin: '50px'}}>
              <img src={'assets/img/comps/comp3.png'} style={{width: '100%'}}/>
              <b>Respect - </b>We treat people as we ourselves expect to be treated, offering love, acceptance and a voice of support in the face of lifeâ€™s challenges.
            </div>
          </div>
          <div style={{display: 'flex', height: '40vh', marginTop: '50px'}}>
            <div style={{flex: 1, flexDirection: 'column', margin: '50px'}}>
              <img src={'assets/img/comps/comp4.png'} style={{width: '100%'}}/>
              <b>Perseverance - </b>We are dedicated to serving people and helping them overcome the challenges they face, no matter how hard it is.
            </div>
            <div style={{flex: 1, flexDirection: 'column', margin: '50px'}}>
              <img src={'assets/img/comps/comp5.png'} style={{width: '100%'}}/>
              <b>Celebration - </b>We recognise and celebrate the efforts and achievements of the people we help, volunteers, supporters and staff.
            </div>
            <div style={{flex: 1, flexDirection: 'column'}}>
            </div>
          </div>
        </div>
      );
    // } else {
    //   return <Redirect to='/login'/>
    // }
  }
}

export default Dashboard;
